import React, {useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import Loader from '../components/Loader'
import Footer from '../components/Footer'
import { Pagination } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import {
    listProducts,
    deleteProduct,
    createProduct,
} from '../actions/productActions'
import { PRODUCT_CREATE_RESET } from '../constants/productConstants'
import Meta from '../components/Meta'

const ProductListScreen = ({ history, match }) => {

    const pageNumber = match.params.pageNumber || 1

    const dispatch = useDispatch()

    const productList = useSelector((state) => state.productList)
    const { loading, error, products, page, pages } = productList

    const productDelete = useSelector((state) => state.productDelete)
    const {
        loading: loadingDelete,
        error: errorDelete,
        success: successDelete,
    } = productDelete

    const productCreate = useSelector((state) => state.productCreate)
    const {
        loading: loadingCreate,
        error: errorCreate,
        success: successCreate,
        product: createdProduct,
    } = productCreate

    const userLogin = useSelector((state) => state.userLogin)
    const { userInfo } = userLogin

    useEffect(() => {
        dispatch({ type: PRODUCT_CREATE_RESET })

        if (!userInfo || !userInfo.isAdmin) {
            history.push('/login')
        }

        if (successCreate) {
            history.push(`/admin/products/${createdProduct._id}/edit`)
        }else {
            dispatch(listProducts('', pageNumber))
        }
    }, [dispatch, history, userInfo, successDelete, successCreate, createdProduct, pageNumber,])

    const deleteHandler = (id) => {
        if (window.confirm('Are you sure')) {
            dispatch(deleteProduct(id))
        }
    }

    const createProductHandler = () => {
        dispatch(createProduct())
    }

    return (
        <>
            <Meta title="Product List" />
            <section className="section-margin calc-60px">
                <div className="container profile_container">
                    <div className="section-intro d-flex justify-content-between">
                        <h5><span className="section-intro__style">All Products</span></h5>
                        <p>
                            <button className='button' onClick={createProductHandler}>
                                <i className='fas fa-plus'></i> New
                            </button>
                        </p>
                    </div>
                    <div>
                        {loadingDelete && <Loader />}
                        {errorDelete && <Message variant='danger'>{errorDelete}</Message>}
                        {loadingCreate && <Loader />}
                        {errorCreate && <Message variant='danger'>{errorCreate}</Message>}
                    </div>
                    <div className="row">
                        <div className="col-md-12 mb-5">
                            {loading ? (
                                <Loader />
                                ) : error ? (
                                <Message variant='danger'>{error}</Message>
                                ) : (
                                    <div className="table-responsive store-table">
                                        <table className="table table-bordered">
                                            <thead>
                                                <tr>
                                                    <th scope="col">Name</th>
                                                    <th scope="col">Price</th>
                                                    <th scope="col">Category</th>
                                                    <th scope="col">Brand</th>
                                                    <th></th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {products.map((product) => (
                                                    <tr key={product._id}>
                                                        <td>{product.name}</td>
                                                        <td>{product.price}</td>
                                                        <td>{product.category}</td>
                                                        <td>{product.brand}</td>
                                                        <td>
                                                            <span className="d-flex justify-content-between">
                                                                <LinkContainer to={`/admin/products/${product._id}/edit`}>
                                                                    <button className='btn btn-sm btn-info'>
                                                                        <i className='fas fa-edit'></i>
                                                                    </button>
                                                                </LinkContainer>
                                                                <button
                                                                    className='btn btn-sm btn-danger ml-2'
                                                                    onClick={() => deleteHandler(product._id)}
                                                                >
                                                                    <i className='fas fa-trash'></i>
                                                                </button>
                                                            </span>
                                                            
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    </div>
                                )
                            }
                        </div>
                    </div>
                    {pages > 1 && (
                        <Pagination>
                            {[...Array(pages).keys()].map((x) => (
                                <LinkContainer
                                    key={x + 1}
                                    to={
                                        `/admin/products/${x + 1}`
                                    }
                                >
                                    <Pagination.Item active={x + 1 === page}>{x + 1}</Pagination.Item>
                                </LinkContainer>
                            ))}
                        </Pagination>
                    )}
                </div>
            </section>
            <Footer />
        </>
    )
}

export default ProductListScreen